import React from "react";
import "../style/form.css";
import { useForm } from "react-hook-form";
import Swal from "sweetalert2";

export default function FormApp() {
  //событие onSubmit вызывает срикпт (в данном сучаем обработанные данные из формы)
  const onSubmit = (data) => {
    console.log(data);
    Swal.fire({
      title: "Data has been sent!",
      text: "Thank you! Data has been sent sucsessfully!",
      icon: "sucsess",
      iconColor: "#17BED2",
      confirmButtonText: "Exit",
      confirmButtonColor: "#17BED2",
    });
  };

  const { register, handleSubmit, errors } = useForm();

  return (
    //принимает функцию обработчик
    <form className="FormApp" onSubmit={handleSubmit(onSubmit)}>
      <div className="form-container">
        <div>
          <input
            name="firstName"
            placeholder="First Name"
            ref={register({ required: true, minLength: 2, maxLength: 12 })}
          />
          {errors.firstName && errors.firstName.type === "required" && (
            <p>This field is required</p>
          )}
          {errors.firstName && errors.firstName.type === "minLength" && (
            <p>This field is needed to be more than 2 symbols</p>
          )}
          {errors.firstName && errors.firstName.type === "maxLength" && (
            <p>This field is needed to be less than 12 symbols</p>
          )}
        </div>

        <div>
          <input
            name="lastName"
            placeholder="Last Name"
            ref={register({ required: true, minLength: 2, maxLength: 12 })}
          />
          {errors.lastName && errors.lastName.type === "required" && (
            <p>This field is required</p>
          )}
          {errors.lastName && errors.lastName.type === "minLength" && (
            <p>This field is needed to be more than 2 symbols</p>
          )}
          {errors.lastName && errors.lastName.type === "maxLength" && (
            <p>This field is needed to be less than 12 symbols</p>
          )}
        </div>

        <div>
          <input
            type="tel"
            placeholder="Mobile number"
            name="Mobilenumber"
            ref={register({
              required: true,
              pattern: /^((8|\+3)[\- ]?)?(\(?\d{3}\)?[\- ]?)?[\d\- ]{11,12}$/,
            })}
          />
          {errors.Mobilenumber && errors.Mobilenumber.type === "required" && (
            <p>This field is required</p>
          )}
          {errors.Mobilenumber && errors.Mobilenumber.type === "pattern" && (
            <p>This field is needed to be in format "+375..." or "8..." </p>
          )}
        </div>

        <div>
          <input
            type="text"
            placeholder="Email"
            name="Email"
            ref={register({ required: true, pattern: /^\S+@\S+$/i })}
          />
          {errors.Email && errors.Email.type === "required" && (
            <p>This field is required</p>
          )}
          {errors.Email && errors.Email.type === "pattern" && (
            <p>Email needed to be pasted in @email format </p>
          )}
        </div>

        <div>
          <textarea
            type="textarea"
            name="Message"
            cols="30"
            rows="5"
            name="Message"
            placeholder="Write to us!"
            ref={register({ required: true, minLength: 10, maxLength: 999 })}
          />
          {errors.Message && errors.Message.type === "required" && (
            <p>This field is required</p>
          )}
          {errors.Message && errors.Message.type === "minLength" && (
            <p>Field sholud be filled at least with 10 symbols </p>
          )}
        </div>

        <div>
          <input
            className="submit-button"
            type="submit"
            name="submit"
            placeholder="Send"
            value="Send"
            ref={register}
          />
        </div>
      </div>
    </form>
  );
}
