import React from 'react';
import { Link } from "react-router-dom";
import '..//components/style/navbar style.css';
import Logo from '../components/logo.png';

export default function Nav() {
  return (
    <div className="main-links">

      <div className="container-header">
        <div className="logo_container">
          <Link to="/">
            <img className="logo" src={Logo} />
          </Link>
        </div>
        <div className="links-nav">
          <li><Link to="/" className="NavBarLinks" >Home</Link></li>
          <li><Link to="/about" className="NavBarLinks">About</Link></li>
          <li><Link to="/work" className="NavBarLinks" >Work</Link></li>
          <li><Link to="/contact" className="NavBarLinks" >Contact</Link></li>
        </div>
      </div>
    </div>
  )
}

