import React from "react";
import "..//components/style/benefits.css";
import { Link } from "react-router-dom";
import { Col, Modal, ModalFooter, ModalBody, Button } from "reactstrap";

export default class ModalWindow extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
      comments: [],
      showModal: false,
    };
  }
  render() {
    return (
      <div className="Link-benefits">
        <Link className="links" to="#" onClick={this.handleModal}>
          READ MORE
        </Link>

        <Modal isOpen={this.state.showModal} toggle={this.handleModal}>
          <ModalBody>
            {this.state.comments.map((comment, index) => {
              if (index == 0 || index == 1 || index == 2) {
                return (
                  <Col key={comment.id}>
                    <div>{comment.body}</div>
                  </Col>
                );
              }
            })}
          </ModalBody>
          <ModalFooter>
            <Button color="secondary" onClick={this.handleModal}>
              Close
            </Button>
          </ModalFooter>
        </Modal>
      </div>
    );
  }

  handleModal = () => {
    this.setState({
      showModal: !this.state.showModal,
    });
  };
  componentDidMount() {
    fetch("https://jsonplaceholder.typicode.com/comments")
      .then((response) => response.json())
      .then((comments) => {
        this.setState({
          comments: [...comments],
        });
      });
  }
}
