import React from 'react';
import {
  GoogleMap,
  useLoadScript,
  Marker,
  InfoWindow,
} from "@react-google-maps/api";
import MapStyles from '..//components/mapStyles'







export default function MyMap(){

const libraries = ["places"];

const { isLoaded, loadError } = useLoadScript({
    googleMapsApiKey: "AIzaSyC-jBRla3CckXOiXHcfO0wKyX6NHcgpRmQ",
    libraries,
  });
const options = {
  styles: MapStyles,
  disableDefaultUI: true,
  zoomControl: true,
};

const [markers, setMarkers] = React.useState([]);
const [selected, setSelected] = React.useState(null);

if (loadError) return "Error";
if (!isLoaded) return "Loading...";


const mapContainerStyle = {
  height: "35vh",
  width: "100%",
};

const center = {
  lat: 53.908995,
  lng: 27.545278,
};

  return (
    <div>

    <GoogleMap  
    mapContainerStyle={mapContainerStyle}
    zoom={10}
    center={center}
    options ={options}
    onClick ={(event)=>{
     
      setMarkers((current)=>[
        ...current,
        {
          lat: event.latLng.lat(),
          lng: event.latLng.lng(),
          time: new Date(),
        }
      ])
    }}
    >
     <Marker key ={1} position={{lat: 53.908995, lng: 27.545278}}
     onClick={()=>
     setSelected(markers)} 
     
     >

{selected ? ( <InfoWindow onCloseClick={()=>(
  setSelected(null)
)} >
       <div>
     <h2>Current Location
       <br></br>
       {center.lat}
       <br></br>
       {center.lng}
       </h2>
       </div>
     </InfoWindow>): null }

   
     </Marker>
  
    {/* {markers.map(marker => <Marker key ={marker.time.toISOString()} position={{lat: marker.lat, lng: marker.lng}} />)} */}
    </GoogleMap>
     </div>
  )
}