import React from 'react';
import '..//style/note.css';

export default function Note() {
    return (
        <div data-aos="slide-up"
            data-aos-offset="200"
            data-aos-delay="50"
            data-aos-duration="1000"
            className="work-container">
            <div className="work-text">
                <div className="work-big-font">Send Us A Note</div>
                <div className="work-small-font">Lorem ipsum dolor sit amet consectetur adipiscing elit sed tempor incididunt ut laboret dolore magna aliqua enim minim veniam exercitation</div>
            </div>
        </div>)
}