import React from 'react';
import NavApp from '../home/components/navbar';
import Note from '..//contact/components/note';
import MyMap from './components/map2';
import FormApp from './components/form';
import Countries from './components/countries';
import FooterNav from '..//home/components/footer-nav';


export default class ContactApp extends React.Component {
    constructor() {
        super()
    }
    render() {
        return (
            <div>
                <NavApp />
                <MyMap />
                <Countries/>
                <Note/>
                <FormApp />
                <FooterNav/>
            </div>
        )
    }
}