import React from 'react';
import '..//components/style/human.css'
import Human_img from '../components/img/human_pic.jpg'

export default class Humans extends React.Component{
    constructor(){
        super()
    }
    render(){
return(
    <div data-aos="fade-up"
    data-aos-offset="200"
    data-aos-delay="50"
    data-aos-duration="1000"
     className ="human_Wrapper">
<img className="Human_img" src={Human_img}/>
    </div>
)
    }
}